.header {
    @apply flex w-full items-center px-4;
    height: 64px;
    border-bottom: 1px solid #eeeeee;
    gap: 16px;

    .logo {
        @apply flex rounded-md justify-center items-center text-white text-lg select-none;
        width: 48px;
        height: 48px;
        background-color: #665ae2;
    }

    .search-bar {
        @apply border bg-gray-100 rounded-md p-4;
        width: 480px;
        height: 48px;
        border-color: #eeeeee;
        transition: all 0.1s;

        &:focus {
            outline: 3px solid #665ae2;
        }
    }

    .add {
        @apply relative flex justify-center items-center rounded-full cursor-pointer;
        width: 42px;
        height: 42px;
        background-color: #7e74e7;

        &>div {
            @apply absolute bg-white;
            width: 45%;
            height: 3px;

            &:last-child {
                transform: rotateZ(90deg)
            }
        }

        &:hover {
            background-color: #4a42a6;
        }
    }

    .space {
        @apply flex-1;
    }

    .avatar {
        @apply relative flex justify-center items-center rounded-full bg-gray-400 text-gray-600 text-2xl cursor-pointer select-none;
        width: 48px;
        height: 48px;

        .user-menu {
            @apply absolute right-0 top-full mt-1 border border-gray-200 bg-white shadow-md rounded-lg w-max overflow-hidden;
            animation: fadeIn 0.25s;

            &>div {
                @apply text-gray-900 text-base py-2 px-4 cursor-default;

                &:last-child {
                    @apply cursor-pointer;

                    &:hover {
                        @apply text-white;
                        background-color: #7e74e7;
                    }
                }
            }
        }
    }
}