.login {
    @apply flex;

    width: 100vw;
    height: 100vh;

    .image {
        background: url("../../images/login_img.jpg");
        background-size: cover;
        width: 50%;
        height: 100%;
    }

    .form-container {
        @apply flex justify-center items-center;

        width: 50%;
        height: 100%;

        .form {
            @apply flex flex-col;
            width: 400px;

            input {
                @apply border rounded-sm p-3 bg-gray-100;
                border-color: #eeeeee;
                margin-bottom: 20px;
                outline: 0 solid #665ae2;
                transition: all 0.1s;
                animation: fadeIn 0.75s;

                &::placeholder {
                    color: #d6d6d6;
                }

                &:focus {
                    outline: 3px solid #665ae2;
                }
            }

            button {
                @apply p-3 text-white rounded-sm;
                background-color: #665ae2;
                width: 180px;
                transition: all 0.1s;
                animation: fadeIn 0.75s;

                &:focus {
                    background-color: #4a42a6;
                    outline: 3px solid #665ae2;
                }
            }

            .error {
                @apply text-red-700 mt-2;
                animation: textShake 0.33s;
            }

            .links {
                @apply flex mb-6;
                border-bottom: 1px solid #eeeeee;
                width: fit-content;
                gap: 1.5rem;

                label {
                    @apply pb-1 text-lg cursor-pointer relative;
                    color: #665ae2;

                    &.selected {
                        &::after {
                            @apply absolute w-full left-0 top-full;
                            content: '';
                            border-style: solid;
                            border-color: #665ae2;
                            border-bottom-width: 3px;
                            animation: borderMax 0.1s;
                        }
                    }
                }
            }
        }
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes borderMax {
    0% {
        border-bottom-width: 0px;
    }

    100% {
        border-bottom-width: 3px;
    }
}

@keyframes textShake {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(1rem, 0);
    }

    75% {
        transform: translate(-1rem, 0);
    }

    100% {
        transform: translate(0, 0);
    }
}