.modal {
    @apply z-50 fixed top-0 left-0 flex justify-center items-center;
    width: 100vw;
    height: 100vh;
    background-color: #00000075;
    animation: fadeIn 0.25s;

    .modal-body {
        @apply relative flex flex-col;

        .modal-head {
            @apply flex justify-between mb-2;

            label {
                @apply text-3xl text-white pl-1;
            }

            .save {
                @apply text-white py-2 px-4 rounded-md select-none cursor-pointer;
                background-color: #665ae2;

                &:hover {
                    background-color: #4a42a6;
                }
            }
        }

        .colors {
            @apply absolute left-full flex flex-col ml-4 mt-14;
            gap: 12px;

            &>div {
                @apply rounded-full cursor-pointer;
                width: 36px;
                height: 36px;

                &:hover {
                    opacity: 0.75;
                }
            }
        }

        textarea {
            @apply rounded-xl bg-gray-200 text-lg p-4;
            width: 400px;
            height: 400px;
            outline: none;
            resize: none;
            transition: all 0.25s;
        }

        .remove {
            @apply w-full text-center bg-red-500 text-white py-2 px-4 rounded-md select-none cursor-pointer mt-2;

            &:hover {
                @apply bg-red-700;
            }
        }
    }
}